.сontainer {
	max-height: 84vh;
	display: flex;
	flex-direction: column;
	gap: 10px;

	
 }
 .scroll {
	width: 650px;
	overflow-y: scroll;
 }
 
 .scroll::-webkit-scrollbar {
	width: 8px;
	background-color: #2f2f37;
 }
 
 .scroll::-webkit-scrollbar-thumb {
	border: 3px solid #8585ad;
	background-color: #8585ad;
 }